.controller{
	display: flex;
	width: 120px;
	height: 60px;
	position: absolute;
	right: 0;
	bottom: 0;
}

.left, .right{
	width: 60px;
	height: 60px;
	display: grid;
	place-items: center;
	cursor: pointer;
	background: black;
}

.left:hover{
	background: hsl(0, 0%, 27%);
}

.right:hover{
	background: hsl(0, 0%, 27%);
}